    import * as React from 'react'

    const data = [
        {name:"Jack",salary:10000},
        {name:"Jones",salary:10000},
        {name:"Jane",salary:5000},
    ]
    const colors = ["White", "Black", "Red", "Green", "Blue"]
    const nums = [1,3,12,412,4,12,4,325,23,5,115,35,32,5]

    export default function Page1() { 

        let items = colors.map((value,index) => <li>{value}</li>)

        const sum = nums.reduce((total, currentValue) => total + currentValue, 0);
        const salarysum = data.reduce((total, person) => total + person.salary, 0 )

        return (
            <>
                <h1>Hello</h1>
                <p>How are you?</p>
                <p>Sum = {sum}</p>
                <p>Sum of Salary = {salarysum}</p>
                <ul>
                    {items}
                </ul>
            </>
        )
    }
